import 'babel-polyfill'
import Vue from 'vue'
import VueHead from 'vue-head'

import App from './components/app.vue'

import router from './router'

Vue.use(VueHead)

//  vue 
// app.vue#app
const app = new Vue({
	el: '#app',
	router,  // routerVue app
	render: h => h(App)
})


